import { Component  } from '@angular/core';
import {  ViewController, IonicPage, NavController, NavParams } from 'ionic-angular';
import { Auth } from '../../../providers';



@IonicPage()
@Component({
  selector: 'page-reglamento-view',
  templateUrl: 'reglamento-view.html',
})
export class ReglamentoViewPage {
  idtipo:any;
  ind1m: any=[];
  idindicador:any;
  idpolitica:any;
  idarea:any;
  opcion:any;
  tituloxx:any;
  ind1ma1:any;
  ind1ma2:any;
  ind1ma3:any;
  tpalabra:any;

  ind2ma1:any;
  ind2ma2:any;
  ind2ma3:any;


  constructor(   public regService: Auth,public viewCtrl: ViewController,  public navParams2: NavParams,public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Detalle1Page');


    


  this.opcion=this.navParams.data.op
  this.tpalabra=this.navParams.data.tpalabra
    if(this.navParams.data.op==1)  
    {
      this.regService.getReg(this.navParams.data.idreglamentod +'/'+ this.navParams.data.idreglamento +'/dbusqueda/','reglamentods').then((data) => {
        this.ind1m=data;
      }, (err) => {   
        //this.msj.showError3('Error',err.error);   
        this.ind1m=[]
      
      });    

    }
    else{


    }
   


  }

  
  openlink(op) {
    window.open(op,'_system', 'location=no');
  }
    

  cancel() {
    this.viewCtrl.dismiss();
  }
}



